import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import Navigation from './components/Navigation';
import Initialize from "./pages/Initialize";
import Overview from "./pages/Initialize";
import Blotter from "./pages/Blotter";
import Survey from "./pages/Survey";
import Footer from "./components/Footer";
import { ThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import defaultTheme from "./themes/default";

const theme = createMuiTheme(defaultTheme);

(window as any).callbacks = {};

navigator.serviceWorker.addEventListener("message", event => {
    if (!event.data) {
        return;
    }

    const { timestamp } = event.data;


    console.debug(`[${ timestamp }] Recieved ${ event.type } message. ${ (window as any).callbacks?.[timestamp] ? "" : "No callback found." } ${ JSON.stringify(event.data) }`);

    if ((window as any).callbacks?.[timestamp]) {
        (window as any).callbacks[timestamp](event.data);
        delete (window as any).callbacks[timestamp];
    }
});

ReactDOM.render(
    <React.StrictMode>
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <Router>
                <>
                    <Navigation />
                    <Switch>
                        <Route exact path="/" component={Initialize} />
                        <Route path="/overview" component={Overview} />
                        <Route path="/blotter" component={Blotter} />
                        <Route path="/survey" component={Survey} />
                    </Switch>
                    <Footer />
                </>
            </Router>
        </ThemeProvider>
    </React.StrictMode>,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
