import React, { useState } from "react";
import Typography from "../../../../components/Typography";
import TextField from "../../../../components/TextField";
import Button from "@material-ui/core/Button";
import InputAdornment from "@material-ui/core/InputAdornment";
import { setValue } from "../../../../util/storage";
import stepStyles from "../styles.module.scss";

const Income = ({ index, goTo, next, stepDifference }: SurveyStepProps) => {
    const [ income, setIncome ] = useState(undefined as number | undefined);

    function onSubmit() {
        setValue("survey", "income", income);

        next();
    }

    const className = stepDifference < 0
        ? stepStyles.previous
        : (
            stepDifference > 0
                ? stepStyles.upcoming
                : stepStyles.current
        );
    
    const buttonAttributes = stepDifference !== 0
        ? { onClick: () => goTo(index) }
        : {};

    return (
        <div className={ className } { ...buttonAttributes }>
            <Typography variant={ stepDifference === 0 ? "h3" : "h6" }>3. Income</Typography>
            { stepDifference === 0 && (
                <div className={ stepStyles.card }>
                    <Typography variant="body1">What is your annual (pre-tax) income?</Typography>
                    <TextField
                        name="income"
                        variant="outlined"
                        type="number"
                        required
                        fullWidth
                        id="income"
                        label="Income"
                        value={ income }
                        onChange={ (e) => setIncome(parseInt(e.target.value)) }
                        InputProps={{
                            startAdornment: <InputAdornment position="start">$</InputAdornment>,
                          }}
                        autoFocus
                        autoComplete="off"
                        className={ stepStyles.topMargin }
                    />
                    <Button variant="outlined" color="primary" onClick={ onSubmit } className={ stepStyles.topMargin }>Continue</Button>
                </div>
            ) }
        </div>
    );
};

export default Income;
