import React, { useState, useEffect } from "react";
import Typography from "../../../../components/Typography";
import Button from "@material-ui/core/Button";
import { getValue } from "../../../../util/storage";
import styles from "./styles.module.scss";
import stepStyles from "../styles.module.scss";

const Prompt = ({ index, goTo, next, skip, stepDifference }: SurveyStepProps) => {
    const [ loaded, setLoaded ] = useState(false);
    const [ complete, setComplete ] = useState(undefined as boolean | undefined);

    useEffect(() => {
        (async () => {
            setComplete(await getValue<boolean>("survey", "complete"));
            setLoaded(true);
        })();
    });

    const className = stepDifference < 0
        ? stepStyles.previous
        : (
            stepDifference > 0
                ? stepStyles.upcoming
                : stepStyles.current
        );

    const buttonAttributes = stepDifference !== 0
        ? { onClick: () => goTo(index) }
        : {};
    
    if (!loaded) {
        return null;
    }

    const text = complete
        ? "Re-do your initial budgeting goals survey?"
        : "Fill out a quick survey to get your budgeting goals started?";

    return (
        <div className={ className } { ...buttonAttributes }>
            <Typography variant={ stepDifference === 0 ? "h3" : "h6" }>1. Take Survey{ complete ? " again" : "" }?</Typography>
            { stepDifference === 0 && (
                <div className={ stepStyles.card }>
                    <Typography variant="body1">{ text }</Typography>
                    <div className={ styles.buttons }>
                        <Button variant="outlined" color="primary" onClick={ next } className={ stepStyles.topMargin }>Continue</Button>
                        <Button variant="outlined" color="primary" onClick={ skip } className={ stepStyles.topMargin }>No Thanks</Button>
                    </div>
                </div>
            ) }
        </div>
    );
};

export default Prompt;
