export async function getValue<T>(table: string, key: string): Promise<T> {
    return new Promise((resolve) => {
        const timestamp = new Date().getTime();

        (window as any).callbacks[timestamp] = ({ value }: { value: T }) => {
            resolve(value);
        };

        console.debug(`[${ timestamp }] Requesting Data: ${ key }`);

        navigator.serviceWorker.controller?.postMessage({ type: "RETRIEVE", table, key, timestamp });
    });
}

export function setValue(table: string, key: string, value: any): void {
    const timestamp = new Date().getTime();

    console.debug(`[${ timestamp }] Sending Data: ${ JSON.stringify({ key, value }) }`);

    navigator.serviceWorker.controller?.postMessage({ type: "STORE", table, key, value, timestamp });
}
