import React from "react";
import Button from "@material-ui/core/Button";
import { setValue } from "../../../../util/storage";
import stepStyles from "../styles.module.scss";

const Step4 = ({ next, stepDifference }: SurveyStepProps) => {
    function onSubmit() {
        setValue("survey", "complete", true);

        next();
    }

    const className = stepDifference < 0
        ? stepStyles.previous
        : (
            stepDifference > 0
                ? stepStyles.upcoming
                : stepStyles.current
        );
    return (
        <div className={ `${ className } ${ stepStyles.card }` }>
            { stepDifference === 0 && <Button variant="outlined" color="primary" onClick={ onSubmit }>Explore my data</Button> }
        </div>
    );
};

export default Step4;
