import React, { useEffect, useState, useCallback } from "react";
import { useHistory } from "react-router-dom";
import Container from "../../components/Container";
import { Bar } from "react-chartjs-2";
import LoadingSpinner from "../../components/LoadingSpinner";
import Prompt from "./steps/Prompt";
import Currency from "./steps/Currency";
import Income from "./steps/Income";
import Expenses from "./steps/Expenses";
import Confirm from "./steps/Confirm";
import styles from "./styles.module.scss";

const RedirectToBlotter = ({ stepDifference }: SurveyStepProps) => {
    const history = useHistory();

    useEffect(() => {
        if (stepDifference !== 0) {
            return;
        }

        history.push("/blotter");
    }, [ history, stepDifference ]);

    return null;
};

const STEPS = [
    Prompt,
    Currency,
    Income,
    Expenses,
    Confirm,
    RedirectToBlotter
];

const Survey = () => {
    const [ loading, setLoading ] = useState(true);
    const [ currentStep, setCurrentStep ] = useState(0);

    const goTo = useCallback((index) => setCurrentStep(index), [ setCurrentStep ]);
    const next = useCallback(() => setCurrentStep(currentStep + 1), [ currentStep, setCurrentStep ]);
    const skip = useCallback(() => setCurrentStep(STEPS.length - 1), [ setCurrentStep ]);

    useEffect(() => {
        setTimeout(() => {
            setLoading(false);
        }, 2000);
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    return (
        <main className={ styles.survey }>
            <LoadingSpinner isLoading={loading} />
            <div className={ styles.container }>
                <Container maxWidth="xs">
                    <form className={styles.form} noValidate>
                        { STEPS.map((StepComponent, index) => <StepComponent key={ index } goTo={ goTo } next={ next } skip={ skip } index={ index } stepDifference={ index - currentStep } />) }
                    </form>
                </Container>
                <Container maxWidth="xs">
                    { currentStep > 1 && (
                        <Bar
                            type="horizontalBar"
                            redraw={ false }
	                        data={{
                                datasets: [{
                                    backgroundColor: ["rgba(255, 99, 132, 1)"],
                                    borderColor: ["rgba(255, 99, 132, 1)"],
                                    borderWidth: 1,
                                    data: [ 12 ],
                                    label: "testing"
                                }],
                                labels: ["test"]
                            }}
                        />)
                    }
                </Container>
            </div>
        </main>
    );
};

export default Survey;
