import React, { useState } from "react";
import Typography from "../../../../components/Typography";
import Autocomplete from "../../../../components/Autocomplete";
import TextField from "../../../../components/TextField";
import Button from "@material-ui/core/Button";
import { setValue } from "../../../../util/storage";
import stepStyles from "../styles.module.scss";

interface Locale {
    locale: string;
    currency: string;
    name: string;
}

const OPTIONS: Locale[] = [{
    locale: "en-US",
    currency: "USD",
    name: "US Dollar"
},{
    locale: "zh-CN",
    currency: "CNY",
    name: "Chinese Yuan"
},{
    locale: "ru-RU",
    currency: "RUB",
    name: "Russian Ruble"
}, {
    locale: "rf-FR",
    currency: "EUR",
    name: "Euro"
}, {
    locale: "es-ES",
    currency: "EUR",
    name: "Euro"
}, {
    locale: "en-GB",
    currency: "GBP",
    name: "British Pound"
}, {
    locale: "de-DE",
    currency: "EUR",
    name: "Euro"
}, {
    locale: "pt-BR",
    currency: "BRL",
    name: "Brazilian Real"
}, {
    locale: "en-CA",
    currency: "CAD",
    name: "Canadian Dollar"
}, {
    locale: "es-MX",
    currency: "MXN",
    name: "Mexican Peso"
}, {
    locale: "it-IT",
    currency: "EUR",
    name: "Euro"
}, {
    locale: "ja-JP",
    currency: "JPY",
    name: "Japanese Yen"
}];

const Currency = ({ index, goTo, next, stepDifference }: SurveyStepProps) => {
    const [data, setData] = useState({} as Locale);

    function onSubmit() {
        setValue("survey", "locale", data);

        next();
    }

    const className = stepDifference < 0
        ? stepStyles.previous
        : (
            stepDifference > 0
                ? stepStyles.upcoming
                : stepStyles.current
        );
    
    const buttonAttributes = stepDifference !== 0
        ? { onClick: () => goTo(index) }
        : {};

    return (
        <div className={ className } { ...buttonAttributes }>
            <Typography variant={ stepDifference === 0 ? "h3" : "h6" }>2. Currency</Typography>
            { stepDifference === 0 && (
                <div className={ stepStyles.card }>
                    <Typography variant="body1">What is your base Country/Currency?</Typography>
                    <Autocomplete
                        id="locale"
                        options={OPTIONS}
                        getOptionLabel={(option) => (option as { name: string }).name}
                        className={ stepStyles.topMargin }
                        renderInput={(params) => <TextField {...params} label="Currency" variant="outlined"/>}
                        onChange={ (e, value) => setData(value as Locale) }
                        value={ data }
                    />
                    <Button variant="outlined" color="primary" onClick={ onSubmit } className={ stepStyles.topMargin }>Continue</Button>
                </div>
            ) }
        </div>
    );
};

export default Currency;
